
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
main {
  padding: 10%;
}
h1,
h2,
h3,
h4 {
  margin-bottom: 50px;
}
.banner {
  width: 100vw;
  height: 100vh;
  padding: 50px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
}
nav * {
  color: #00db54;
}
section {
  padding: 50px 0;
  min-height: 100vh;
}
section h1 {
    text-decoration: underline;
    text-align: center;
}
section h2 {
    color: #7801ff;
}
hr {
  border-color: #757575;
}
.home-banner {
  background-image: url("/assets/img/home-banner.jpg");
  color: white;
}
.about {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.about p {
    text-align: center;
}
.skill {
  min-height: 70vh;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.skill:nth-child(even) h2 {
    text-align: right;
}
.skill:nth-child(even) ul {
    float: right;
}
.skill ul {
    list-style: none;
}
.service {
  min-height: 70vh;
}
.service p {
    text-align: justify;
}
.service:nth-child(even) h2 {
    text-align: right;
}
